.projects{
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
    background: #000011;
    padding: 10em 0;
}
.project-overlay, .projects-overlay{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: var(--orange);
    z-index: 20;
}
.projects h1{
    font-size: 10rem;
    color: var(--font-clr);
    margin: 0;
    width: 50%;
    line-height: 0.8em;
}
.projects-p{
    color: white;
    margin: 0;
    width: 50%;
    line-height: 1.5em;
}
.projects-links{
    width: 80%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    margin-top: 5em;
}
.projects-link{
    text-decoration: none;
    position: relative;
    color: white;
    display: flex;
    align-items: center;
    gap: 1em;
    border-bottom: 1px solid var(--font-clr);
    padding: 1em 0;
    transition: all 0.3s;
}
.projects-link:hover{
    background: var(--orange);
}
.projects-link:hover p, small{
    color: var(--blue-dark);
}
.projects-link p{
    font-size: 2rem;
    width: max-content;
    margin: 0;
}
.projects-link small{
    position: absolute;
    right: 0;
    color: var(--orange);
}


/* Single project component */
.project{
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3em;
    background: #000011;
    padding: 120vh 0 10em 0;
}
.project-img-wrapper-cover{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
}
.project-img-wrapper-cover div{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: linear-gradient(0deg, rgba(0,0,17,1) 0%, rgba(0,0,13,0.8972631288843662) 37%, rgba(0,0,11,0.8132295154389881) 64%, rgba(0,0,6,0.47429394120929624) 82%, rgba(0,0,0,0) 100%);
}
.project-img-wrapper-cover img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.project-text{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3em;
    padding: 15em 0;
}
.project h1{
    width: 80%;
    font-size: 8rem;
    line-height: 0.9em;
    color: white;
    text-align: center;
}
.project-desc, .project-sub{
    color: white;
    width: 60%;
    text-align: center;
    line-height: 1.5em;
}
.project small{
    color: var(--orange);
}
.project-video-wrapper{
    width: 90%;
    border-radius: var(--radius);
    background: black;
    margin: 2em;
    border-radius: var(--radius);
    overflow: hidden;
}
.project-video-wrapper video{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.project-imgs-wrapper{
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5em;
    margin: 2em;
}
.project-imgs-wrapper div{
    width: 100%;
    border-radius: var(--radius);
    overflow: hidden;
}
.project-imgs-wrapper div img{
    object-fit: cover;
    width: 100%;
    height: 100%;
}


.project-buttons{
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.project-prev-btn, .project-next-btn{
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 0.5em;
    font-size: 1.2rem;
    color: var(--blue-dark);
    background: var(--orange);
    border: 1px solid var(--orange);
    border-radius: var(--radius);
    padding: 1em 2em;
    cursor: pointer;
    transition: all 0.3s;
}
.project-prev-btn:hover{
    border: 1px solid var(--orange);
    background: var(--blue-dark);
    color: var(--orange);
}

.project-fallback{
    width: 100%;
    height: 5px;
    background: rgb(0, 0, 0);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
}


/* MEDIA QUERIES */
@media only screen and (max-width: 480px){
    .projects h1, .projects-p, .project h1, .project-desc{
        width: 90%;
        text-align: center;
    }
    .projects h1, .project h1{
        font-size: 4rem;
    }
    .projects-links{
        width: 90%;
    }
    .projects-link p{
        font-size: 1.2rem;
        text-align: left;
    }
    .project-video-wrapper, .project-img-wrapper{
        width: 100%;
        height: 450px;
    }
    .project-prev-btn, .project-next-btn{
        font-size: 1rem;
    }
    .project{
        padding-top: 100vh;
    }

    .project-imgs-wrapper div{
        width: 100%;
    }

}