.loading {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--blue-dark);
    z-index: 30;
}
.loading p{
    position: absolute;
    top: 40%;
    left: 50%;
    font-size: 1rem;
    transform: translate(-50%, -40%);
    color: var(--orange);
}
.loading-span{
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -60%);
    font-size: 2rem;
    color: var(--orange);
}
.loading h1{
  width: 100%;
  position: absolute;
  bottom: -35%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 20rem;
  white-space: nowrap;
  font-weight: 900;
  color: var(--orange);
  text-transform: uppercase;
  text-align: center;
  font-family: 'Bebas Neue', cursive;
}

@-webkit-keyframes fly-by {
    0% {
      opacity: 0;
      -webkit-transform: translateX(-200%) scale(0);
        transform: translateX(-200%) scale(0); }
    50% {
      opacity: 1;
      -webkit-transform: translateX(0) scale(1.25);
        transform: translateX(0) scale(1.25); }
    100% {
      opacity: 0;
      -webkit-transform: translateX(200%) scale(0);
        transform: translateX(200%) scale(0); } }
@keyframes fly-by {
    0% {
      opacity: 0;
      -webkit-transform: translateX(-200%) scale(0);
        transform: translateX(-200%) scale(0); }
    50% {
      opacity: 1;
      -webkit-transform: translateX(0) scale(1.25);
        transform: translateX(0) scale(1.25); }
    100% {
      opacity: 0;
      -webkit-transform: translateX(200%) scale(0);
        transform: translateX(200%) scale(0); } }
  

.fly-by{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.fly-by:before {
    -webkit-animation: fly-by 1s infinite ease;
            animation: fly-by 1s infinite ease;
    background-color: var(--orange);
    border-radius: 100%;
    display: block;
    content: '';
    height: 40px;
    width: 40px; 
}

@media only screen and (max-width: 480px){
  .loading h1{
    font-size: 6.5rem;
    bottom: -5%;
    white-space: nowrap;
  }
  .loading p{
    text-align: center;
  }
}