.sidebar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column-reverse;
    z-index: 11;
}


.sidebar-link{
    width: 100%;
    height: 20%;
    text-decoration: none;
    display: flex;
    align-items: center;
    overflow: hidden;
    cursor: pointer;
    padding: 1em;
    background: #000011;
    border-bottom: 1px solid #000033;
    transition: background 0.3s;
}
.sidebar-link:hover .sidebar-link-p{
    color: var(--blue-dark);
}
.sidebar-link-p{
    font-size: 4rem;
    margin: 0;
    color: var(--font-clr);
    font-weight: 600;
    text-transform: uppercase;
    /* font-family: 'Bebas Neue', cursive; */
}
