#link-4:hover, #link-3:hover, #link-2:hover, #link-1:hover{
    background: var(--orange);
}


.sidebar-socials{
    width: 100%;
    height: 20%;
    overflow: hidden;
    display: flex;
    align-items: center;
    gap: 1em;
    padding: 1em;
    background: #000011;
}
.sidebar-socials button{
    background: transparent;
    border: none;
    color: var(--orange);
    font-size: 1.2rem;
    cursor: pointer;
    transition: all 0.2s;
}
.sidebar-socials button:hover{
    transform: scale(1.1);
}

/* MEDIA QUERIES */
@media only screen and (max-width: 480px){
    .sidebar-link p{
        font-size: 6rem;
    }
}