.about{
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5em;
    background: var(--blue-dark);
    padding: 10em 0;
}
.about-overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: var(--orange);
    z-index: 20;
}

.about h1{
    font-size: 10rem;
    line-height: 0.7em;
    color: var(--font-clr);
    margin: 0;
    width: 60%;
    user-select: none;
}
.about h2{
    font-size: 5rem;
    line-height: 0.7em;
    color: var(--font-clr);
    margin: 0;
    width: 60%;
    user-select: none;
}
.about-first, .about-second{
    color: var(--font-clr);;
    margin: 0;
    width: 60%;
    line-height: 1.5em;
    user-select: none;
}
.about span{
    color: var(--orange);
    text-transform: uppercase;
}

.about-skills{
    width: 60%;
    display: flex;
    flex-flow: row wrap;
    gap: 1em;
    overflow: hidden;
}
.about-skills div{
    height: max-content;
    background: var(--orange);
    padding: 0.5em 1em;
    border: 1px solid var(--orange);
    border-radius: 35px;
    color: var(--blue-dark);
    font-size: 2rem;
    user-select: none;
}
.about-skills div:nth-child(even){
    background: transparent;
    color: var(--orange);
}

/* MEDIA QUERIES */
@media only screen and (max-width: 480px){
    .about h1, .about-first, .about-second{
        width: 90%;
    }
    .about h1{
        font-size: 7rem;
    }
    .about-skills{
        width: 90%;
    }
    .about-skills div{
        font-size: 1.2rem;
    }
}