.nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    padding: 4em;
    display: flex;
    align-self: center;
    gap: 2em;
}


/* Logo */
.logo{
    width: 2em;
    height: 2em;
    overflow: hidden;
}
.logo img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}


/* Nav links */
.nav-links-wrapper{
    display: flex;
    align-items: center;
    gap: 1em;
}


.nav-socials{
    position: absolute;
    right: 8em;
    top: 4.5em;
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.nav-socials button{
    background: transparent;
    color: white;
    border: none;
    font-size: 18px;
}


.nav-links{
    position: relative;
    text-decoration: none;
    color: white;
    display: none;
    align-items: center;
    text-transform: uppercase;
}
.nav-links::before{
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -10px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    color: white;
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    transition: all 0.2s;
}
.nav-links:hover::before{
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}


/* Nav linkes animated character */
.nav-links-character{
    position: relative;
    transition: transform 0.2s;
    color: white;
    overflow: hidden;
}

#character-K::before, #character-KK::before{
    content: "k";
    position: absolute;
    bottom: 0;
    left: 0;
    pointer-events: none;
    color: white;
    transition: transform 0.2s;
}
#character-K::after, #character-KK::after{
    content: "k";
    position: absolute;
    bottom: -25px;
    left: 0;
    pointer-events: none;
    color: white;
    transition: transform 0.2s;
}
#character-E::before{
    content: "e";
    position: absolute;
    bottom: 0;
    left: 0;
    pointer-events: none;
    color: white;
    transition: transform 0.2s;
}
#character-E::after{
    content: "e";
    position: absolute;
    bottom: -25px;
    left: 0;
    pointer-events: none;
    color: white;
    transition: transform 0.2s;
}
.nav-links:hover .nav-links-character::after{
    transform: translateY(-25px);
}
.nav-links:hover .nav-links-character::before{
    transform: translateY(-25px);
}



/* Sidebar open button */
.nav-sidebar-btn, .nav-sidebar-btn-active{
    position: fixed;
    right: 4em;
    top: 4em;
    width: 40px;
    height: 40px;
    border-radius: var(--radius);
    background: white;
    color: var(--blue-dark);
    cursor: pointer;
    transition: all 0.2s;
    z-index: 12;
}
.nav-sidebar-btn-active{
    transform: rotate(-90deg);
}
.nav-sidebar-btn:hover, .nav-sidebar-btn-active:hover{
    background: var(--orange);
}
.nav-sidebar-btn::before{
    content: "";
    position: absolute;
    left: 7px;
    top: 16px;
    width: 25px;
    height: 2px;
    background: black;
    transition: all 0.2s;
}
.nav-sidebar-btn:hover::before{
    top: 14px;
}
.nav-sidebar-btn::after{
    content: "";
    position: absolute;
    left: 7px;
    bottom: 16px;
    width: 25px;
    height: 2px;
    background: black;
    transition: all 0.2s;
}
.nav-sidebar-btn:hover::after{
    bottom: 14px;
}

.nav-sidebar-btn-active::before{
    content: "";
    position: absolute;
    left: 7px;
    top: 19px;
    width: 25px;
    height: 2px;
    background: black;
    transform: rotate(-45deg);
}
.nav-sidebar-btn-active::after{
    content: "";
    position: absolute;
    left: 7px;
    bottom: 19px;
    width: 25px;
    height: 2px;
    background: black;
    transform: rotate(45deg);
}

@media only screen and (max-width: 480px){
    .nav{
        padding: 3em;
    }
    .nav-sidebar-btn, .nav-sidebar-btn-active{
        right: 3em;
        top: 3em;
    }
    .nav-socials{
        display: none;
    }
}