footer{
    position: absolute;
    bottom: 0;
    left: 0;
    background: var(--blue-dark);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5em 4em;
    border-top: 1px solid var(--orange);
}

.footer-copyright{
    color: var(--orange);
    display: flex;
    align-items: center;
    gap: 0.2em;
}
.footer-copyright p{
    font-size: 0.7rem;
    color: var(--orange);
}

.footer-socials{
    display: flex;
    align-items: center;
    gap: 1em;
}
.footer-socials-p{
    color: var(--orange);
    font-size: 0.8rem;
    margin: 0;
}
.footer-links{
    position: relative;
    color: var(--orange);
    font-size: 0.9rem;
    text-decoration: none;
    height: 1.6em;
    display: flex;
    align-items: flex-end;
    margin: 0;
}
.footer-links:hover::before{
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}
.footer-links::before{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background: var(--orange);
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    transition: all 0.3s;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 480px){
    footer{
        flex-direction: column-reverse;
        gap: 0.5em
    }
    footer{
        padding: 1.5em;
    }
}