:root{
  --main-clr: #ffffff;
  --font-clr: #bebcbc;
  --font-light: #cfcfcf;
  --blue: #2222ff;
  --blue-dark: #000011;
  --orange: #FFDD00;
  --radius: 5px;
}

*{
  box-sizing: border-box;
}

html, body{
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Bebas Neue', cursive;
  background: #000011;
}
body{
  overflow-x: hidden;
}

body h1{
  font-family: 'Raleway', sans-serif;
  color: white;
  margin: 0;
}

body p, small, span, label{
  font-family: 'Raleway', sans-serif;
  margin: 0;
}
