.home{
    width: 100%;
    height: 100vh;
}

.canvas{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
}
.cylinder-image-wrapper{
    width: 3em;
    height: 3em;
    border: 1px solid white;
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.427);
    user-select: none;
    display: flex;
    align-items: center;
    justify-content: center;
}
.cylinder-image-wrapper p{
    color: white;
    font-size: 0.7rem;
}

.home-text{
    width: 25%;
    position: fixed;
    left: 4em;
    bottom: 4em;
    z-index: 2;
    display: flex;
    flex-direction: column;
    gap: 1em;
    user-select: none;
}
.home-text h1{
    font-size: 2rem;
    margin: 0;
    text-align: left;
    line-height: 0.8em;
    color: white;
}
.home-text p{
    text-align: left;
    color: white;
}
.home-text span{
    color: var(--orange);
}
.home-text button{
    width: max-content;
    border: none;
    border-radius: var(--radius);
    color: var(--orange);
    background: transparent;
    font-size: 1.2rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 0.5em;
    transition: all 0.3s;
}
.home-text button:hover{
    background: var(--blue-dark);
    color: var(--orange);
}

.home-info{
    position: fixed;
    bottom: 5em;
    left: 50%;
    transform: translateX(-50%);
    color: var(--blue-dark);
    z-index: 2;
    display: flex;
    align-items: center;
    gap: 0.5em;
    user-select: none;
}
.home-info div{
    width: 3em;
    height: 3em;
    border: 2px solid white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.home-info-icon{
    width: 2.2em;
    height: 2.2em;
    border: 1px solid var(--orange);
    color: var(--blue-dark);
    background: white;
    border-radius: 50%;
}
.home-info small{
    font-size: 1.2rem;
    user-select: none;
    color: white;
}